<template>
  <nav class="nav">
    <a href="#" class="logo">
      <img src="@/assets/images/logo.svg" alt="" class="logo__img">
      <span class="logo__span">vue weather</span>
    </a>
    <div class="nav__search">
      <img src="@/assets/images/kaplya.svg" alt="">
      <input 
        type="text" 
        class="nav__search-input"
        placeholder="Выбрать город"
        v-model="cityName"
        @keydown.enter="getWeather(cityName), cityName = ''"
      >
    </div>
  </nav>
</template>

<script>

import { mapActions } from 'vuex'

  export default {
    data() {
      return {
        cityName: ''
      }
    },
    methods: {
      ...mapActions(['getWeather'])
    }
  }
</script>